<!--反馈信息 模块-->
<template>
  <div class="box" style="width: 100%">
    <div class="type">
      <div class="text">状态:</div>
      <el-radio-group v-model="radio2">
        <el-radio :label="1">未出发</el-radio>
        <el-radio :label="2">行进中</el-radio>
        <el-radio :label="3">到达</el-radio>
      </el-radio-group>
    </div>
    <div class="bz">
      <div class="text">备注:</div>
      <el-input
        class="mytextarea"
        type="textarea"
        :rows="6"
        placeholder="请输入详细内容"
        resize="none"
        v-model="textarea"
      ></el-input>
    </div>
    <!-- <el-upload
  class="upload-demo"
  action="https://jsonplaceholder.typicode.com/posts/"
  :on-preview="handlePreview"
  :on-remove="handleRemove"
  :before-remove="beforeRemove"
  multiple
  :limit="3"
  :on-exceed="handleExceed"
  :file-list="fileList">
  <el-button size="small" type="primary">点击上传附件</el-button>
    </el-upload>-->
    <div class="btn-group1">
      <el-button size="medium" type="primary" @click="clickFk">反馈</el-button>
      <el-button size="medium" type="primary" @click="clickFk1">取消</el-button>
    </div>
  </div>
</template>

<script>
import { builderParams } from '@/utils/builderParams'
import { post, get } from '@/utils/request'
// import { eventProcessCommandReplay } from '../../api/visual'
export default {
  data () {
    return {
      radio2: 1,
      textarea: ''
    }
  },
  props: {
    cmdId: {
      type: String,
      default: ''
    },
    notificationId: {
      type: String,
      default: ''
    },
    point: {
      type: String,
      default: ''
    }
  },
  methods: {
    getuuid () {
      return ((((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1))
    },
    getNowTime () {
      return new Date(+new Date() + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '')
    },
    clickFeedBack () {
      // let table = {
      //   DataBase: 'yjzh'
      //   // DynamicTable
      // }
    },
    async clickFk () {
      // 1 未出发  2行进中  3 到达
      const arr = this.point.substring(this.point.indexOf('(') + 1, this.point.indexOf(')')).split(' ')
      let point = {
        x: arr[0],
        y: arr[1]
      }
      var taskList = []
      var fkData = {
        id: this.getuuid(),
        cmdId: this.cmdId,
        content: this.textarea,
        contentType: 'text',
        createrId: '0001',
        createrName: '李铭',
        createrUnitId: '630100000001',
        createrUnitName: '蓟州区政府',
        source: 'zhdd',
        timestamp: this.getNowTime(),
        x: parseFloat(point.x),
        y: parseFloat(point.y),
        notificationId: this.notificationId,
        state: this.radio2
      }
      taskList.push(builderParams.basesave('cmdfeedback', fkData))
      let conditions = [{ Property: 'Id', Oper: 'eq', Value: this.notificationId }]
      let updateNo = builderParams.baseupdate('cmdnotification', { state: 7 }, conditions)
      taskList.push(updateNo)
      // 获取json中的地址
      const configuration = await get({ url: 'static/config.json', data: {} })
      const eventProcessCommandReplay = configuration.visual.baseUrl + configuration.visual.eventProcessCommandReplay
      var table = builderParams.tranparams(taskList)
      if (this.notificationId) {
        const taskResult = await post({ url: builderParams.tranExecUrl(), data: table })
        if (taskResult.Result === 0) {
          // 上报事件处置指令反馈—多次推送
          var task = {
            // 编号
            bh: this.getuuid(),
            // 系统名称
            xtmc: 'zhdd',
            // 调用地址
            jkdz: eventProcessCommandReplay,
            // 调用类型
            ajaxType: 'POST',
            // 请求参数
            param: '',
            // 状态
            state: 0,
            // 入库时间戳
            rksjc: this.getNowTime(),
            // 更新时间
            gxsj: null,
            // 操作人编号
            gxrbh: '0001',
            // 操作人
            gxr: '应急管理局'
          }
          var parma = {
            id: fkData.id,
            processCommandId: this.cmdId,
            content: this.textarea,
            occurTime: fkData.timestamp
          }
          task.param = JSON.stringify(parma)
          let taskItem = builderParams.save('quartz', task)
          taskItem.DataBase = 'duty'
          let taskResult = await post({ url: builderParams.execUrl(), data: taskItem })
          if (taskResult.Result !== 0) {
            alert('投大屏任务接口请求失败')
          }
          // alert('反馈成功！')
          this.$message({
            message: '反馈成功！',
            type: 'success'
          });
          this.$layer.closeAll()
        }
      }
    },
    clickFk1 () {
      this.$layer.closeAll()
    }
  },
  mounted () {
    // alert( this.notificationId + ' ------' + this.cmdId)
  }
}
</script>

<style lang="stylus">
.mytextarea {
  width: 314px;
  box-sizing: border-box;
  border-radius: 4px;
  background: rgba(255, 255, 255, 0.1);
}

.mytextarea textarea {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid #02e1e8;
  color: #fff;
}

.btn-group1 {
  text-align: center;
  margin-top: 15px;
}

.upload-demo {
  margin-top: 10px;
}

.el-radio {
  color: #fff;
  cursor: pointer;
  margin-right: 30px;
}

.type {
  margin: 10px 0 8px 0;

  .text {
    display: inline-block;
    width: 40px;
    height: 20px;
    color: #fff;
  }
}

.bz {
  .text {
    display: inline-block;
    width: 40px;
    height: 24px;
    margin: 0px 0px 110px 0px;
  }
}
</style>



