<!--任务详情 模块-->
<template>
  <div class="box" style="width: 100%">
    <el-table
      :cell-style="rowstyle"
      :row-style="rowstyle"
      :header-cell-style="cellstyle"
      :header-row-style="headerstyle"
      :data="tableData3"
      style="width: 100%; background-color: transparent !important;"
    >
      <el-table-column label="任务详情">
        <el-table-column prop="name" label="下令人" width="100"></el-table-column>
        <el-table-column prop="unit" label="下令单位" width="120"></el-table-column>
        <el-table-column prop="duty" label="职责" width="120"></el-table-column>
        <el-table-column prop="time" label="下令时间" width="160"></el-table-column>
        <el-table-column prop="message" label="指令内容"></el-table-column>
      </el-table-column>
    </el-table>

    <el-table
      :cell-style="rowstyle"
      :row-style="rowstyle"
      :header-cell-style="cellstyle"
      :header-row-style="headerstyle"
      :data="tableData4"
      style="width: 100%; background-color: transparent !important;"
    >
      <el-table-column label="反馈信息">
        <el-table-column prop="name" label="填写人" width="120"></el-table-column>
        <el-table-column prop="time" label="反馈时间" width="180"></el-table-column>
        <el-table-column prop="url" label="反馈内容">
          <template slot-scope="scope">
            <!-- <el-popover
            placement="right"
            title=""
            trigger="click"
            popper-class="mypopover">
          <div slot="reference" v-html="scope.row.message" style="max-height: 600px;max-width: 600px"></div>
          <el-image src="http://1.119.195.59:89/ProviderMap/Images/marker/cgc.png"></el-image>
            </el-popover>-->
            <div v-html="scope.row.message" @click="imgClick(scope.row)"></div>
          </template>
        </el-table-column>
        <el-table-column label="路径规划">
          <template scope="scope">
            <img
              :src="scope.row.pathUrl"
              width="40"
              height="40"
              class="head_pic"
              @click="mapPath(scope.row)"
            />
          </template>
        </el-table-column>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { builderParams } from '@/utils/builderParams'
import { post, get } from '@/utils/request'
import audioImg from '@/assets/images/feedback/audio.png'
import videoImg from '@/assets/images/feedback/video.png'
export default {
  data () {
    return {
      tableData3: [{
        name: '',
        unit: '',
        duty: '',
        time: '',
        message: ''
      }],
      tableData4: [{
        name: '',
        time: '',
        message: '',
        contentType: '',
        url: ''
      },
      {
        name: '',
        time: '',
        message: '',
        contentType: '',
        url: ''
      }]
      // imgdata: require ("../../assets/images/feedback/fkxq.jpg")
    }
  },
  props: ['cmdId', 'taskId', 'eventId'],
  methods: {
    rowstyle () {
      return 'background-color: transparent; color: #88d6ff;text-align: center;'
    },
    cellstyle ({ row, column, rowIndex, columnIndex }) {
      // if(column.property === 'status') {
      //   switch (row.status) {
      //     case '已签收': return {backgroundColor: 'transparent',textAlign: 'center',color: '#67C23A',border: 0}
      //     break
      //     case '等签收': return {background: '#F56C6C', color: '#FFF',border: 0}
      //     break
      //     default:  return 'background-color: transparent; border: 0; color: #88d6ff;text-align: center;'
      //   }
      // }
      return 'background-color: transparent; text-align: center; color: #00f1e5; font-size: 16px; border-bottom: 1px solid white;'
    },
    headerstyle () {
      return 'background-color: transparent; color: #00f1e5; border-bottom: 1px solid white text-align: center; font-size: 16px;'
    },
    async loadTaskFeedback () {
      this.tableData4 = []
      let cmdConditions = [{ Property: 'Id', Oper: 'eq', Value: this.cmdId }]
      let cmdTable = builderParams.query('cmd', '', 'id', 'asc', cmdConditions)

      let cmdResult = await post({ url: builderParams.queryUrl(), data: cmdTable })
      if (cmdResult.Result === 0) {
        if (cmdResult.Data.length > 0) {
          this.tableData3[0].name = cmdResult.Data[0].Creatername
          this.tableData3[0].unit = cmdResult.Data[0].Createrunitname
          this.tableData3[0].time = cmdResult.Data[0].Starttime.replace('T', ' ')
          this.tableData3[0].time = this.tableData3[0].time.substring(0, this.tableData3[0].time.length - 2)
          this.tableData3[0].message = cmdResult.Data[0].Content
        }
      }
      let fkconditions = [{ Property: 'notificationId', Oper: 'eq', Value: this.taskId }]
      let fkTable = builderParams.query('cmdFeedback', '', 'timestamp', 'asc', fkconditions)

      let fkResult = await post({ url: builderParams.queryUrl(), data: fkTable })
      let fkData = {}
      if (fkResult.Result === 0) {
        fkResult.Data.forEach(element => {
          fkData = {
            name: element.Creatername,
            time: element.Timestamp.replace('T', ' '),
            // 反馈内容
            message: element.Content,
            // 内容类型
            contentType: element.Contenttype,
            // 附件(img, video ,audio) 路径
            url: element.Content
          }
          if (fkData.contentType === 'image') {
            fkData.message = '<image src="' + element.Content + '" width="50" height="50"/>'
          }
          if (fkData.contentType === 'sound') {
            fkData.message = '<image src="' + audioImg + '" width="50px" height="50px">'
          }
          if (fkData.contentType === 'video') {
            fkData.message = '<image src="' + videoImg + '" width="50px" height="50px">'
          }
          fkData.time = fkData.time.substring(0, fkData.time.length - 2)
          fkData.pathUrl = '@/../static/arcgismap/assets/img/luxian.png'
          fkData.x = element.X
          fkData.y = element.Y
          this.tableData4.push(fkData)
        })
      }
    },
    async mapPath (row) {
      if (row.x && row.y) {
        let startPoint = ''
        let endPoint = ''
        const configuration = await get({ url: 'static/config.json', data: {} })
        let conditions = [{ Property: 'Id', Oper: 'eq', Value: this.eventId }]
        let item = builderParams.get('Event', '', conditions)
        item.DataBase = 'duty'
        const result = await post({ url: builderParams.getUrl(), data: item })
        if (result.Result === 0) {
          if (result.Data.length > 0) {
            endPoint = result.Data[0].Dwxzb + ',' + result.Data[0].Dwyzb
          }
        }
        startPoint = row.x + ',' + row.y
        let MapPlanUrl = configuration.Map.MapPlan.replace('{startPoint}', startPoint).replace('{endPoint}', endPoint)
        let pointData = await get({ url: MapPlanUrl, data: {} })
        if (pointData.TransAnalystInfo) {
          let pointList = pointData.TransAnalystInfo.pathAnalystCoords
          let MapLine = 'LINESTRING('
          pointList.forEach(element => {
            MapLine += element.x + ' ' + element.y + ','
          });
          MapLine = MapLine.substring(0, MapLine.length - 1)
          MapLine += ')'
          // 规划路线
          this.$map.drawLine({
            pointlist: MapLine,
            click: '',
            onsuccess: '',
            layerId: this.$map.constslayer.mapPath,
            style: {
              color: '#00FF7F',
              fontSize: 12,
              lineStyle: 'solid',
              opacity: 0.8,
              borderWidth: 1,
              borderColor: '#00FF7F',
              background: '#00FF7F',
              fillStyle: 'solid'
            }
          })
        } else {
          alert('路径规划接口错误, 反馈坐标可能不在天津市')
        }
        this.$map.drawPointToMap({
          x: row.x,
          y: row.y,
          ico: { name: './static/arcgismap/assets/img/car.png', height: 30, width: 31 },
          layerId: this.$map.constslayer.alarmLayer,
          isCenter: true,
          isWindow: true,
          url: '#',
          text: '',
          click: () => {
          }
        })
        this.$map.setViewCenter({ x: row.x, y: row.y, level: 14 })
      } else {
        alert('该反馈没有定位坐标！')
      }
    },
    imgClick (obj) {
      // 反馈内容为附件
      if (obj.contentType !== 'text') {
        window.open(obj.url)
      }
    }
  },
  mounted () {
    this.loadTaskFeedback()
  }
}
</script>

<style lang="stylus">
.mytextarea {
  box-sizing: border-box;
  border-radius: 4px;
  background: rgba(255, 255, 255, 0.1);
}

.mytextarea textarea {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid #02e1e8;
  color: #fff;
}

.el-table::before {
  height: 0;
}

.btn-group {
  width: 100%;
  // text-align: center;
  margin-top: 20px;
}

.btn-group img {
  width: 250px;
}

.mypopover img {
  width: 400px;
  // height : 400px;
}

.el-table--fit {
  border: solid 1px #11c2ff;
  background: rgba(17, 194, 255, 0.1);
}
</style>



