    //     }
    //   }
    // },
    // 任务展示
    async loadTask () {
      this.listdata = []
      let conditions = [{ Property: 'caseid', Oper: 'eq', Value: this.eventId }]
      let item = builderParams.query('cmd', '', 'Sendertime', 'desc', conditions, 'View')
      let cmdResult = await post({ url: builderParams.queryUrl(), data: item })
      if (cmdResult.Result === 0) {
        if (cmdResult.Data.length > 0) {
          cmdResult.Data.forEach(item => {
            let taskInfo = {}
            taskInfo.name = item.Accepterunitname
            // 0 1黄 3绿  4红色
            taskInfo.state = item.State
            if (item.State === '1') {
              taskInfo.state = 1
            } else if (item.State === '7') {
              taskInfo.state = 3
            } else {
              taskInfo.state = 1
            }
            // 通知Id
            taskInfo.taskId = item.Notifyid
            // 指令id
            taskInfo.cmdId = item.Id
            this.listdata.push(taskInfo)
          })
        }
      }
    },
    getuuid () {
      return ((((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1))
    },
    getNowTime () {
      return new Date(+new Date() + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '')
    },
    // 复选框 change 获取单位复选框选中的值
    checkinlist (val) {
      // 单位名称 | 单位编号
      // alert(this.checkList1)
    },
    // async loadSchemeSource (type) {
    //   this.tableData = []
