    async cmdMsg (body) {
      // const cmdTable = {
      //   DataBase: 'yjzh',
      //   DynamicQuery: {
      //     QueryArgs: {
      //       Order: 'caseId',
      //       Sort: 'desc',
      //       Conditions: [
      //         { Property: 'cmdId', Oper: 'eq', Value: body.CaseId },
      //         { Property: 'accepterunitId', Oper: 'eq', Value: body.UnitId },
      //         { Property: 'caseId', Oper: 'eq', Value: this.eventId }
      //       ]          },
      //     TableView: 'zhdd_taskmsg',
      //     Type: 'View'
      //   }
      // }
      // var result = await postTask(cmdTable)
      // if (result.Result === 0) {
      //   for (let index in result.Data) {
      //     this.listdata.forEach(item => {
      //       if (item.taskId === result.Data[index].cmdnotificationId) {
      //         item.state = 3
      //       }
      //     })
      //   }
      // }
      const notificationId = body.NotificationId
      this.listdata.forEach(p => {
        const state = p.state
        if (p.taskId === notificationId) {
          this.setIntervalList.push({
            timingId: setInterval(() => {
              if (p.state === state) {
                p.state = 4
              } else {
                p.state = state
              }
            }, 500),
            taskId: notificationId
          })
        }
      })
    },
   // 事件续报
  async getRenewlist () {
      // alert(this.eventId)
      if (this.eventId) {
      let conditions = [{ Property: 'relId', Oper: 'eq', Value: this.eventId }]
      var table = builderParams.get('Resubmit', '', conditions)
      table.DataBase = 'duty'
        const event = await post({url: builderParams.getUrl(), data: table})
        if (event.Result === 0) {
          if (event.Data.length > 0) {
            // let eventInfo = event.Data[1]
            event.Data.forEach(item => {
              this.renewlist.push(item)
            })
          }
        }
      }
    },
    renewdetial (data) {
      // alert(1);
      let id = this.$layer.iframe({
        content: {
          content: RenewCpn,
          parent: this,
          data: {info: data}
        },
        shade: false,
        area: ['400px', '360px'],
        offset: ['600', '580'],
        title: '事件续报'
      })
      this.layerId = id
    }
  },
  watch: {
    curr: function (value) {
      // if (value === 0) {
      //   // 救援队伍
      //   this.loadSchemeSource('team')
      // } else if (value === 1) {
      //   // 救援专家
      //   this.loadSchemeSource('expert')
      // } else if (value === 2) {
      //   // 组织机构
      //   this.loadSchemeSource('jg')
      // }
    },
    cur: function (value) {
      if (value === 1) {
        this.loadGroup()
      }
    }
  },
  mounted () {
    this.eventId = this.$route.query.eventId
    this.initEventMap()
    this.getEventInfo()
    this.getRenewlist()
    // this.loadUnit()
    // this.loadSchemeSource('team')
    this.loadTask()
    // this.loadGroup()
    // websocket 接受消息事件
    this.$ws.acceptMsg = msg => {
      console.log(msg)
      if (typeof msg === 'string') {
        msg = JSON.parse(msg)
      }
      var msgBody = {}
      switch (msg.BusinessKey) {
        case 'mobile_cmd':
          msgBody = msg.BusinessData
          switch (msgBody.stype) {
            case 'command-feedback':
              const body = JSON.parse(msg.BusinessData.body)
              // 消息提示，当前任务有反馈信息
              this.cmdMsg(body)
              break
          }
          ; break
      }
      // this.loadTask()
    }
  },
  components: {
    // 'map-tool': Maptools,
    // 'time-tool': Timetools,
    // 'v-title': Title,
    'control': Control,
    'scheduled-Resources': ScheduledResources,
    'add-Resources': AddResources
  }
}
</script>
