<template>
    <el-table
    :data="tableData"
    :show-header="false"
    :row-style= 'rowstyle'
    :cell-style= 'rowstyle'
    style="width: 100%; background-color: transparent !important;">
        <el-table-column
        label=""
        align="center"
        prop="dataname"
        width="120"
        ></el-table-column>
        <el-table-column
        prop="datavalue"
        width="245"
        :show-overflow-tooltip="true"
        ></el-table-column>
    </el-table>
</template>

<script>
export default {
  data () {
    return {
      tableData: [{
        dataname: '创建时间:',
        datavalue: ''
      }, {
        dataname: '请求资源情况:',
        datavalue: ''
      }, {
        dataname: '事件发展情况:',
        datavalue: ''
      }, {
        dataname: '报告人编号:',
        datavalue: ''
      }, {
        dataname: '报告人姓名:',
        datavalue: ''
      }, {
        dataname: '联系电话:',
        datavalue: ''
      }]
        }
  },
  props: ['info'],
  methods: {
    handle () {
      // let item = {};
      this.tableData[0].datavalue = this.info.Createtime
      this.tableData[1].datavalue = this.info.Support
      this.tableData[2].datavalue = this.info.Development
      this.tableData[3].datavalue = this.info.Userid
      this.tableData[4].datavalue = this.info.Username
      this.tableData[5].datavalue = this.info.Phone
      // this.tableData.push(item)
      // console.log(this.info);
    },
    rowstyle ({row, rowIndex}) {
      return 'background-color: transparent; border: 0; color: #88d6ff; row:hover : black'
    }
  },
  mounted () {
    this.handle()
    }
}
</script>

<style lang="stylus">
el-table{
  background : transparent !important;
}
.el-table::before{
  height : 0;
}
</style>



