<template>
  <!-- 已调度资源组件 右下角 -->
  <div class="wrapperdc">
    <el-row :gutter="15">
      <el-col :span="4" :offset="6">
        <label>{{data2+ ' '+ ':'}}</label>
      </el-col>
      <el-col :span="10" :offset="0">
        <el-input v-model="input2" :readonly="true" placeholder="请输入内容"></el-input>
      </el-col>
    </el-row>

    <el-row :gutter="15">
      <el-col :span="4" :offset="6">
        <label>{{data3+ ' '+ ':'}}</label>
      </el-col>
      <el-col :span="10" :offset="0">
        <el-input v-model="input3" placeholder="请输入内容"></el-input>
      </el-col>
    </el-row>
    <el-row :gutter="15">
      <el-col :span="4" :offset="6">
        <label>{{data4+ ' '+ ':'}}</label>
      </el-col>
      <el-col :span="10" :offset="0">
        <el-input v-model="input4" placeholder="请输入内容"></el-input>
      </el-col>
    </el-row>
    <!-- <el-row :gutter="20"> -->
    <!-- 
      <el-col offset="6">
        <el-button-group>
          <el-button type="primary" size="middle" >取消派遣</el-button>
          <el-button type="primary" size="middle" @click="close">退出</el-button>
        </el-button-group>
    </el-col>-->
    <el-row :gutter="15">
      <el-col :span="4" :offset="6">
        <label>{{data1+ ' '+ ':'}}</label>
      </el-col>
      <el-col :span="10" :offset="0">
        <!-- <el-input v-model="input1" placeholder="请输入内容" suffix-icon="el-icon-phone"></el-input> -->
        <el-input v-model="input1" placeholder="请输入联系电话">
          <i class="el-icon-phone" slot="suffix" @click="callPhone"></i>
        </el-input>
      </el-col>
      <!-- <el-col :span="4">
        <el-button type="primary" size="middle">拨打电话</el-button>
      </el-col>-->
    </el-row>

    <el-row :gutter="15" type="flex" align="bottom">
      <el-col :span="10" :offset="10">
        <el-input type="textarea" :rows="3" placeholder="请输入短信内容" v-model="noteContent"></el-input>
      </el-col>
      <el-col>
        <el-button :span="0" type="primary" size="middle">发送短信</el-button>
      </el-col>
    </el-row>

    <el-row :gutter="20">
      <!-- <el-col offset="8">
        <el-button type="primary" size="middle">发送短信</el-button>
      </el-col> -->
      <el-col offset="10">
        <el-button type="primary" size="middle" @click="updateData">保存</el-button>
      </el-col>
      <el-col>
        <el-button type="primary" size="middle" @click="cancel">取消派遣</el-button>
      </el-col>
    </el-row>
    <!-- <el-button type="primary" size="mini" >取消派遣</el-button>
    <el-button type="primary" size="mini" @click="close">退出</el-button>-->
  </div>
</template>

<script>
import { builderParams } from '@/utils/builderParams'
import { post, get } from '@/utils/request'
export default {
  data () {
    return {
      data1: '',         // 4
      data2: '资源类型',  // 1
      data3: '',    // 2
      data4: '',      // 3
      data5: '',
      input1: '',
      input2: '',
      input3: '',
      input4: '',
      input5: '',
      noteContent: ''
    }
  },
  props: {
    row: {
      type: Object,
      default: {}
    }
  },
  methods: {
    callPhone () {
      alert('手机号为：' + this.input1 + '   正在呼叫。。。')
    },
    close () {
      this.$layer.closeAll();
    },
    loadInfo () {
      const row = this.row
      if (row.date === '队伍' || row.date === '专家') {
        this.data4 = row.date + '名称'
        this.data1 = row.date + '电话'
        this.input2 = this.row.date
        this.input1 = this.row.tel
        this.data3 = row.date + '类型'
        this.input3 = this.row.dispatchType
        this.input4 = this.row.name
        this.input5 = this.row.date
      } else if (row.date === '机构') {
        this.data1 = '联系电话'
        this.data3 = '机构名称'
        this.data4 = '负责人'
        this.input2 = '组织机构'
        this.input1 = this.row.tel
        this.input3 = this.row.name
        this.input4 = row.person
        this.input5 = '组织机构'
      } else {
        this.input2 = '物资'
        this.data3 = '物资名称'
        this.input3 = this.row.name
        this.data4 = '数量'
        // var number = row.name.replace(/[^0-9]/ig, "")
        this.input4 = this.row.number
        this.data1 = '联系电话'
        this.input1 = this.row.tel
      }
    },
    async cancel () {
      const row = this.row
      if (!this.row.uuid) {
        return null
      }
      var task = {}
      let conditions = [{ Property: "Id", Oper: "eq", Value: this.row.uuid }]

      if (row.date === '专家') {
        task = builderParams.delete('exper', conditions)
      } else if (row.date === '队伍') {
        task = builderParams.delete('ranks', conditions)
      } else if (row.date === '车辆') {
        task = builderParams.delete('car', conditions)
      } else if (row.date === '机构') {
        task = builderParams.delete('enterp', conditions)
      } else {
        task = builderParams.delete('resoure', conditions)
      }
      if (task != {}) {
        var result = await post({ url: builderParams.execUrl(), data: task })
        if (result.Result === 0) {
          // alert('撤销派遣成功！')
          this.$message({
            message: '撤销派遣成功! ',
            type: 'success'
          })
          this.$parent.loadDispatch(this.$parent.eventId)
          this.close()
        }
      } else {
        // alert('撤销派遣异常！')
          this.$message({
            message: '撤销派遣异常! ',
            type: 'warning'
          })
      }
    },
    // 保存
    async updateData () {
      const row = this.row
      console.log(row);
      
      if (!this.row.uuid) {
        return null
      }
      var task = {}
      let conditions = [{ Property: "Id", Oper: "eq", Value: this.row.uuid }]

      if (row.date === '专家') {
        let item = {
          expertName: this.input4,
          tel: this.input1,
          experttype: this.input3
        }
        task = builderParams.update('exper', item, conditions)
      } else if (row.date === '机构') {
        let item = {
          enterpName: this.input3,
          tel: this.input1,
          enterpdelegate: this.input4
        }
        task = builderParams.update('enterp', item, conditions)
      } else if (row.date === '队伍') {
        let item = {
          ranksName: this.input4,
          tel: this.input1,
          ranksType: this.input3
        }
        task = builderParams.update('ranks', item, conditions)
      } else {
        let item = {
          dispatchcount: this.input4,
          tel: this.input1,
          resname: this.input3
        }
        task = builderParams.update('resoure', item, conditions)
      }
      if (task != {}) {
        // 编号有值时再去update
        if (task.DynamicTable.Conditions[0].Value) {
          var result = await post({ url: builderParams.execUrl(), data: task })
          if (result.Result === 0) {
            // alert('保存成功！')
            this.$message({
              message: '保存成功! ',
              type: 'success'
            })
            this.$parent.loadDispatch(this.$parent.eventId)
            this.close()
          }
        }
      }
    }
  },
  mounted () {
    this.loadInfo()
  }
}
</script>

<style lang='stylus'>
.wrapperdc .el-input__suffix {
    right: 10px;
    top: 13px;
    -webkit-transition: all .3s;
    transition: all .3s;
}
.wrapperdc .el-textarea__inner {
  // background-color: rgba(61, 103, 160, 0.6) !important;
  background-color: transparent !important;
  // border: 0 !important;
  color: #fff !important;
}
</style>

<style lang="stylus" scoped>
.el-input__suffix {
    right: 10px;
    top: 13px;
    -webkit-transition: all .3s;
    transition: all .3s;
}
.wrapperdc {
  width: 100%;

  .el-row {
    width: 90%;
    margin-top: 20px;

    label {
      line-height: 40px;
    }
  }
}

.el-col-24 {
  width: 20%;
}

.el-row:nth-child(6) {
  margin-top: 35px;
}

.el-textarea__inner {
  // background-color: rgba(61, 103, 160, 0.6) !important;
  background-color: transparent !important;
  border: 0 !important;
  color: #fff !important;
}
</style>



