<template>
  <div class="video-wrap" ref="live" id="playercontainer">请检查浏览器是否打开了flash功能！</div>
  <!-- <video id="myPlayer" poster="" controls playsInline webkit-playsinline autoplay></video> -->
</template>

<script>
import websocket from '../../../static/player/cyberplayer.js'
export default {
  data () {
    return {
      player: Function
    }
  },
  methods: {
    baiduMp4 () {
      // this.player = cyberplayer('myPlayer').setup({
      //   width: 680,
      //   height: 448,
      //   isLive: true,
      //   file: 'rtmp://42.96.189.40:6063/hls/968', // 多码率hls地址
      //   autostart: true,
      //   stretching: 'uniform',
      //   volume: 100,
      //   controls: true,
      //   ak: '4923077915824a5fb269a468800c204e' // 公有云平台注册即可获得accessKey
      // })

      this.player = cyberplayer(this.$refs.live).setup({
        width: 650, // 宽度，也可以支持百分比(不过父元素宽度要有)
        height: 520, // 高度，也可以支持百分比
        // file: 'http://gcqq450f71eywn6bv7u.exp.bcevod.com/mda-hcztxwn9bkc94r0z/mda-hcztxwn9bkc94r0z.m3u8',
        file: 'rtmp://42.96.189.40:6063/hls/968', // 播放地址
        autostart: true, // 是否自动播放
        stretching: 'uniform', // 拉伸设置
        repeat: false, // 是否重复播放
        volume: 100, // 音量
        controls: true, // controlbar是否显示
        starttime: 0, // 视频开始播放时间点(单位s)，如果不设置，则可以从上次播放时间点续播
        ak: '4923077915824a5fb269a468800c204e' // 公有云平台注册即可获得accessKey
      })
    }
    // 百度解析视频播放方法
    // baiduMp4 () {
    //   // 这里前面是用var声明的player，现在把player直接存在data()里面
    //   this.player = cyberplayer(this.$refs.live).setup({
    //     width: 315, // 宽度，也可以支持百分比(不过父元素宽度要有)
    //     height: 568, // 高度，也可以支持百分比
    //     file: 'rtmp://42.96.189.40:6063/hls/968', // 播放地址
    //     autostart: true, // 是否自动播放
    //     stretching: 'uniform', // 拉伸设置
    //     repeat: false, // 是否重复播放
    //     volume: 100, // 音量
    //     controls: true, // controlbar是否显示
    //     starttime: 0, // 视频开始播放时间点(单位s)，如果不设置，则可以从上次播放时间点续播
    //     ak: '***' // 公有云平台注册即可获得accessKey
    //   })
    //   // this.playComplete()
    // }
  },

  mounted () {
    this.baiduMp4()
    //   var player = cyberplayer('playercontainer').setup({
    //     width: 680,
    //     height: 448,
    //     isLive: true,
    //     //      file: "http://gcqq450f71eywn6bv7u.exp.bcevod.com/mda-hcztxwn9bkc94r0z/mda-hcztxwn9bkc94r0z.m3u8", // 多码率hls地址
    //     file: 'rtmp://42.96.189.40:6063/hls/968', // 多码率hls地址
    //     autostart: true,
    //     stretching: 'uniform',
    //     volume: 100,
    //     controls: true,
    //     ak: '4923077915824a5fb269a468800c204e' // 公有云平台注册即可获得accessKey
    //   })
  },
  // 实例销毁之前清除player方法，停止播放
  beforeDestroy () {
    this.player.remove()
  },
  // 实例销毁之后移除所有的监听器和完全销毁这些实例，清除它与其它实例的连接，解绑全部指令及监视器
  destroyed () {
    this.$off()
    this.$destroy()
  },
  components: {
    websocket
  }
}
</script>
<style lang="stylus" scoped>
.video-wrap {
  width 100%
  color: #fff;
  text-align center
  padding: 62px 0 0 0;
}
</style>



