<template>
  <!-- 已调度资源组件 右下角 -->
  <div class="wrapper">
    <div class="table-content">
      <el-table
      :data="tableData" style="width: 100%"
      :header-cell-style="headerstyle"
      :cell-style="cellstyle"
      :show-header="showHeader" height="254"
      @row-click="dispatchinfo">
        <el-table-column prop="date" label="资源类型" width="80" ></el-table-column>
        <el-table-column prop="name" label="名称" width="120" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="address" label="是否预案派遣"></el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import { builderParams } from '@/utils/builderParams'
import { post, get } from '@/utils/request'
import DispatchDetails from '../../components/DispatchDetails'
export default {
  data () {
    return {
      tableData: [
      //    {
      //   date: '探测仪',
      //   name: '',
      //   address: '120件'
      // }
      ],
      showHeader: true // 是否显示表头
    }
  },
  props: {
    eventId: {
      type: String,
      default: ''
    }
  },
  methods: {
    // 头部样式
    headerstyle () {
      return 'text-align: center; color: #00f1e5'
    },
    // body样式
    cellstyle () {
      return 'text-align: center;'
    },
    async loadDispatch (eventId) {
      this.tableData = []
      let conditions = [{Property: 'eventId', Oper: 'eq', Value: eventId}]
      let resourcesItem = builderParams.query('Dispatch', '', 'createtime', 'desc', conditions, 'View')
      const result = await post({url: builderParams.queryUrl(), data: resourcesItem})
      if (result.Result === 0) {
        result.Data.forEach(element => {
          let isScheme = ''
          if (element.Matching === 1) {
            isScheme = '是'
          } else {
            isScheme = '否'
          }
          let item = {}
          if (element.Typename === '车辆') {
            item.uuid = element.Id
            item.date = element.Typename
            item.name = element.dispatchid
            item.address = isScheme
            item.dispatchtime = element.Dispatchtime
            item.person = element.Person
            item.tel = element.Tel
            item.Createname = element.Createname
            item.Createunitname = element.Createunitname
          } else if (element.Typename === '机构') {
            item.uuid = element.Id
            item.date = element.Typename
            item.name = element.dispatchname
            item.address = isScheme
            item.dispatchtime = element.Dispatchtime
            item.person = element.Person
            item.tel = element.Tel
            item.Createname = element.Createname
            item.Createunitname = element.Createunitname
          } else if (element.Typename === '专家') {
            item.uuid = element.Id
            item.date = element.Typename
            item.name = element.dispatchname
            item.address = isScheme
            item.dispatchtime = element.Dispatchtime
            item.person = element.Person
            item.tel = element.Tel
            item.Createname = element.Createname
            item.Createunitname = element.Createunitname
            item.dispatchType = element.dispatchtype
          } else if (element.Typename === '队伍') {
            item.uuid = element.Id
            item.date = element.Typename
            item.name = element.dispatchname
            item.address = isScheme
            item.dispatchtime = element.Dispatchtime
            item.person = element.Person
            item.tel = element.Tel
            item.Createname = element.Createname,
            item.dispatchType = element.dispatchtype
            item.Createunitname = element.Createunitname
          } else if (element.Typename === '物资') {
            item.uuid = element.Id
            item.date = element.Typename
            item.name = element.dispatchname
            item.address = isScheme
            item.dispatchtime = element.dispatchtime
            item.person = element.Person
            item.tel = element.Tel
            item.Createname = element.Createname,
            item.Createunitname = element.Createunitname
            item.number = element.Count
          }
          this.tableData.push(item)
        })
      }
    },
    dispatchinfo (row) {
      let id = this.$layer.iframe({
        content: {
          content: DispatchDetails, // 传递的组件对象
          parent: this, // 当前的vue对象
          data: { row: row }// props
        },
        shade: false, // 是否显示遮罩
        area: ['700px', '490px'],
        offset: ['1000', '450'],
        title: '调度资源详情'
      })
      this.layerId = id
    }
  },
  watch: {
    // 第一次跳转加载已调度资源物资
    eventId: function (newval, oldval) {
      if (newval) {
        this.loadDispatch(newval)
      }
    }
  },
  mounted () {
    // 第二次切回预案响应，重新加载已调度资源
    if (this.eventId) {
      this.loadDispatch(this.eventId)
    }
  }
}
</script>
<style lang="stylus">
.wrapper .el-table {
  color: #fff;
}

.wrapper .el-table, .el-table__expanded-cell {
  background-color: transparent;
  // opacity:0;
}

.wrapper .el-table--enable-row-hover .el-table__body tr:hover>td {
  background-color: #409EFF;
}

.wrapper .table-content .el-table td, .el-table th.is-leaf {
  border-bottom: 0px solid #EBEEF5;
}

.wrapper .table-content .el-table td:nth-child(1) {
  color: #14CAFA;
}

.wrapper .el-table td, .el-table th {
  padding: 4px 0;
  min-width: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-overflow: ellipsis;
  vertical-align: middle;
  position: relative;
  text-align: left;
}

.wrapper .el-table th, .el-table tr {
  background-color: rgba(17, 194, 255, 0.15) !important;
}

// 最后一个横线样式
.wrapper .el-table--border::after, .el-table--group::after, .el-table::before {
  content: '';
  position: absolute;
  background-color: rgba(17, 194, 255, 0.15) !important;
  z-index: 1;
}

</style>
<style lang="stylus" scoped>
.wrapper {
  pointer-events: auto;
  .table-content {
    width: 120%;
    // height: 202px;
    margin: 2px 0 0 4px;
    border: solid 1px #217ca0;
    background: rgba(17, 194, 255, 0.15);
    overflow-y: scroll;
  }
}
</style>



