<!-- 预案指挥 指挥调度 右上角 指挥调度发任务 组件 -->
<template>
  <!-- 指挥调度 -->
  <div class="items">
    <v-title title="指挥调度"></v-title>
    <div class="content1">
      <div class="video-img">
        <ul class="tab-tilte">
          <li @click="cur = 0" :class="{ active: cur == 0 }">已派遣</li>
          <li @click="cur = 1" :class="{ active: cur == 1 }">未派遣</li>
        </ul>
        <div class="tab-content">
          <div v-show="cur == 1">
            <div class="title"></div>
            <div class="bottom">
              <el-checkbox-group v-model="checkList1" @change="checkinlist(val)">
                <el-checkbox
                  v-for="(check,index) in checkboxData"
                  :key="index"
                  :label="check.name+'|'+ check.id"
                  :title="check.name"
                >
                  {{ check.name | unitName }}
                  <!-- <span class="ypq">{{ check.pq }}</span> 派遣状态-->
                  <span class="video" @click.stop="layerVideo()">视频</span>
                </el-checkbox>
              </el-checkbox-group>
            </div>
            <el-form ref="form" :model="form" label-width="80px" class="my-input-textarea">
              <el-form-item label>
                <el-input
                  type="textarea"
                  v-model="form.desc"
                  :autosize="{ minRows: 5, maxRows: 18 }"
                  placeholder="请输入内容"
                ></el-input>
              </el-form-item>
            </el-form>
            <div class="btn-group">
              <el-button type="primary" size="mini" @click="send()">发送任务</el-button>
              <el-button type="primary" size="mini">一键拨打</el-button>
              <el-button type="primary" size="mini">发送短信</el-button>
            </div>
          </div>
          <div v-show="cur == 0">
            <div class="title" @click="loadGroup"></div>
            <!-- checkbox 标题-->
            <div class="bottom">
              <el-checkbox-group v-model="groupList" @change="checkinlist(val)">
                <el-checkbox
                  v-for="(check,index) in groupData"
                  :key="index"
                  :label="check.name+'|'+check.id"
                  :title="check.name"
                >
                  {{ check.name }}
                  <!-- <span class="ypq">{{ check.pq}}</span> 派遣状态 -->
                  <span class="video" @click.stop="layerVideo()">视频</span>
                </el-checkbox>
              </el-checkbox-group>
            </div>
            <el-form ref="form" :model="form" label-width="80px" class="my-input-textarea">
              <el-form-item label>
                <el-input
                  type="textarea"
                  v-model="form.desc"
                  :autosize="{ minRows: 5, maxRows: 18 }"
                  placeholder="请输入内容"
                ></el-input>
              </el-form-item>
            </el-form>
            <div class="btn-group">
              <el-button type="primary" size="mini" @click="send('group')">发送任务</el-button>
              <el-button type="primary" size="mini">一键拨打</el-button>
              <el-button type="primary" size="mini">发送短信</el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Title from '../../components/Title'
import Video from '../../components/Video'
import { builderParams } from '@/utils/builderParams'
import { post, get } from '@/utils/request'
// import { eventProcessCommand } from '../../api/visual'
export default {
  data () {
    return {
      // 未派遣单位列表
      checkboxData: [],
      cur: 0, // 默认选中第一个tab
      curr: 0,
      checkList: [],
      // checkList: [], // 复选框单位名称
      // 未派遣单位 选中集合
      checkList1: [],
      form: {
        desc: '' // 文本框
      },
      // 已派遣单位列表
      groupData: [],
      groupList: []
    }
  },
  props: {
    eventId: {
      type: String,
      default: ''
    }
  },
  methods: {
    getuuid () {
      return ((((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1))
    },
    getNowTime () {
      return new Date(+new Date() + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '')
    },
    // 发送任务
    async send (type) {
      if (type === 'group') {
        alert('所选单位已派遣！ ')
        return null
      }
      if (this.checkList1.length <= 0) {
        return null
      }
      const configuration = await get({url: 'static/config.json', data: {}})
      var checkedUnit = []
      this.checkList1.forEach(item => {
        checkedUnit.push({
          unitId: item.split('|')[1],
          unitName: item.split('|')[0]
        })
        this.groupData.push({
          id: item.split('|')[1],
          name: item.split('|')[0],
          pq: ''
        })
      })
      var cmdContent = this.form.desc
      // 指令和通知
      var parmeter = []
      // 投大屏 任务表
      var quartz = []
      var cmd = {
          Id: this.getuuid(),
          CaseId: this.eventId,
          Stype: 'task',
          Title: '',
          Content: cmdContent,
          ContentType: 'text',
          State: '1',
          Property: '0',
          CreaterId: '0001',
          CreaterName: '王鸣',
          CreaterUnitId: '',
          CreaterUnitName: '和平区政府',
          Source: 'zhdd',
          TimeStamp: this.getNowTime(),
          StartTime: this.getNowTime()   
      }
      parmeter.push(builderParams.basesave('cmd', cmd))
      checkedUnit.forEach(item => {
        var notification = {
            Id: this.getuuid(),
            CmdId: cmd.Id,
            SenderId: '0001',
            SenderName: '王鸣',
            SenderUnitId: '应急办',
            Accepterid: '',
            AccepterName: '',
            AccepterUnitid: item.unitId,
            AccepterUnitName: item.unitName,
            SenderTime: this.getNowTime(),
            Source: 'zhdd',
            TimeStamp: this.getNowTime(),
            State: '1',
            SendType: '1'
        }
        const eventProcessCommand = configuration.visual.baseUrl + configuration.visual.eventProcessCommand
        // 上报事件处置指令 投大屏
        var task = {
          // 编号
          bh: this.getuuid(),
          // 系统名称
          xtmc: 'zhdd',
          // 调用地址
          jkdz: eventProcessCommand,
          // 调用类型
          ajaxType: 'POST',
          // 请求参数
          param: '',
          // 状态
          state: 0,
          // 入库时间戳
          rksjc: this.getNowTime(),
          // 更新时间
          gxsj: null,
          // 操作人编号
          gxrbh: '0001',
          // 操作人
          gxr: '应急管理局'
        }
        var param = {
          eventId: this.eventId,
          id: cmd.Id,
          content: cmdContent,
          receiveOrg: item.unitId,
          commandTime: this.getNowTime()
        }
        task.param = JSON.stringify(param)
        // 上报事件处置指令 投大屏
        quartz.push(builderParams.basesave('quartz', task))
        // 指令通知
        parmeter.push(builderParams.basesave('cmdnotification', notification))
      })
      // // 根据派遣单位的编号从未派遣中删除
      for (let item in this.checkboxData) {
        let flag = 0
        checkedUnit.forEach(p => {
          if (this.checkboxData[item].id === p.unitId) {
            flag = 1
          }
        })
        if (flag === 1) {
          this.checkboxData.splice(item, 1)
        }
      }
      // //  投大屏 任务表
      quartz = builderParams.tranparams(quartz)
      quartz.DataBase = 'duty'
      var visualResult = await post({ url: builderParams.tranExecUrl(), data: quartz })
      // 保存指令 和通知
      var result = await post({url: builderParams.tranExecUrl(), data: builderParams.tranparams(parmeter)})
      if (result.Result === 0) {
        alert('发送成功！')
        if(this.$parent.loadTask) {
          this.$parent.loadTask()
        }
        var msgData = {
          BusinessKey: 'zhdd_cmd_sendu',
          BusinessId: 'abcd',
          BusinessData: ''
        }
        checkedUnit.forEach(item => {
          this.$ws.send(msgData, item.unitId, 'unit')
        })
      }
      this.checkList1 = []
    },
    // 获取复选框选中的值
    checkinlist (val) {
      // var check = this.checkList
      // console.log(this.checkList)
    },
    async loadGroup (value) {
      // // 查询指挥调度组
      // const table = {
      //   DataBase: 'yjzh',
      //   DynamicQuery: {
      //     QueryArgs: { Order: 'Createtime', Sort: 'desc', Conditions: [] },
      //     TableView: 'group',
      //     Type: 'Table'
      //   }
      // }
      // let groupResult = await postTask(table)
      // if (groupResult.Result === 0) {
      //   groupResult.Data.forEach(item => {
      //     this.groupData.push({
      //       id: item.Id,
      //       name: item.Groupname,
      //       pq: '已派遣'
      //     })
      //   })
      // }
      // 查询已调派单位
      if (value === 0) {
        this.groupData = []
        let dispatchConditions = [{ Property: 'CaseId', Oper: 'eq', Value: this.eventId }]
        let dispatchItem = builderParams.query('cmd', '', 'SenderTime', 'desc', dispatchConditions, 'View')

        let result = await post({url: builderParams.queryUrl(), data: dispatchItem})
        if (result.Result === 0) {
          if (result.Data.length > 0) {
            result.Data.forEach(item => {
              this.groupData.push({
                id: item.Accepterunitid,
                name: item.Accepterunitname,
                pq: ''
              })
            })
          }
        }
      }
      // 未派遣单位
      if (value === 1) {
        this.checkboxData = []
        let unitItem = builderParams.query('unit', '', 'id', 'asc', [])
        unitItem.DataBase = 'yhtx'
        let unitResult = await post({url: builderParams.queryUrl(), data: unitItem})
        if (unitResult.Result === 0) {
          // 所有单位
          unitResult.Data.forEach(item => {
            let flag = 0
            // 已派遣单位
            this.groupData.forEach(p => {
              if (item.Id === p.id) {
                flag = 1
              }
            })
            if (flag === 0) {
              this.checkboxData.push({
                id: item.Id,
                name: item.Description,
                pq: ''
              })
            }
          })
        }
      }
    },
    // 弹出视频直播
    layerVideo () {
      let id = this.$layer.iframe({
        content: {
          content: Video, // 传递的组件对象
          parent: this, // 当前的vue对象
          data: {}// props
        },
        shade: false, // 是否显示遮罩
        area: ['700px', '600px'],
        offset: ['960', '520'],
        title: ''
      })
      this.layerId = id
    }
  },
  components: {
    'v-title': Title,
    Video
  },
  mounted () {
  },
  filters: {
    unitName (value) {
      if (value.length > 10) {
        return value.slice(0, 10) + '...'
      }
      return value
    }
  },
  watch: {
    cur: function (value) {
      // 0 已派遣  1 未派遣 
      this.loadGroup(value)
    },
    eventId: function (newval, oldval) {
      if (this.eventId) {
        this.loadGroup(0)
      }
    }
  }
}
</script>

<style lang="stylus">
// element ui样式
.my-input-textarea .el-textarea__inner {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid #02e1e8;
  color: #fff;
  width: 288px;
  height: 117px;
}

.content-table .el-table {
  color: #fff;
}

.content-table .el-table, .el-table__expanded-cell {
  background-color: transparent;
  // opacity:0;
}

.content-table .el-table--enable-row-hover .el-table__body tr:hover>td {
  background-color: #409EFF;
}

.content-table .table-content .el-table td, .el-table th.is-leaf {
  border-bottom: 0px solid #EBEEF5;
}

.content-table .table-content .el-table td:nth-child(1) {
  color: #14CAFA;
}

.content-table .el-table td, .el-table th {
  padding: 4px 0;
  min-width: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-overflow: ellipsis;
  vertical-align: middle;
  position: relative;
  text-align: left;
}

.content-table .el-table th, .el-table tr {
  background-color: rgba(17, 194, 255, 0.15) !important;
}

// 最后一个横线样式
.content-table .el-table--border::after, .el-table--group::after, .el-table::before {
  content: '';
  position: absolute;
  background-color: rgba(17, 194, 255, 0.15) !important;
  z-index: 1;
}

// input的样式
.el-input__inner {
  -webkit-appearance: none;
  background-color: rgba(61, 103, 160, 0.6);
  background-image: none;
  border-radius: 4px;
  border: 1px solid rgba(61, 103, 160, 0.6);
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: 0;
  padding: 0 15px;
  -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}

.el-input-group__append, .el-input-group__prepend {
  background-color: #1F97E9;
  color: #fff;
  vertical-align: middle;
  display: table-cell;
  position: relative;
  border: 1px solid #1F97E9;
  border-radius: 4px;
  padding: 0 20px;
  width: 1px;
  white-space: nowrap;
}

.el-tabs__nav-scroll {
  width: 94px;
  overflow: hidden;
}

.el-tabs__item.is-active {
  color: #EBBB2F;
}

.el-tabs__item {
  padding: 0 34px;
  height: 40px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  line-height: 40px;
  display: inline-block;
  list-style: none;
  font-size: 14px;
  font-weight: 500;
  color: #303133;
  position: relative;
}
</style>
<style lang="stylus" scoped>
// 指挥调度
.items {
  height: 57%;
  margin: -5% 0px 0px 12px;
  z-index: 2;

  .title {
    .img {
      width: 200px;
      height: 50px;
      background: url('../../assets/images/home/zhihuidiaodu.png') no-repeat;
    }
  }

  .content1 {
    .video-img {
      width: 314px;
      height: 440px;
      margin: -8px 0 0 40px;
      padding: 2px 0 0 0;
      border: solid 1px #217ca0;
      background: rgba(17, 194, 255, 0.15);

      // background: url('../../assets/images/home/right_one.png') no-repeat;
      .tab-tilte {
        width: 94%;
        margin: 10px 0 0 10px;
      }

      .tab-tilte li {
        float: left;
        width: 50%;
        height: 10px;
        line-height: 10px;
        padding: 10px 0;
        text-align: center;
        background-color: #507696;
        cursor: pointer;
        border-radius: 20px 20px 0 0px;
        color: #7bc3ff;
      }

      /* 点击对应的标题添加对应的背景颜色 */
      .tab-tilte .active {
        background-color: #1a91f5;
        color: #fff;
      }

      .tab-content div {
        float: left;
        width: 100%;
        margin: 4px 0 0 0;

        .title {
          color: #fff;
          margin: 14px 0px 0 10px;
          font-size: 18px;
        }

        .bottom {
          height: 180px;
          overflow-y: scroll;
        }

        .el-checkbox {
          width: 240px;
          color: #fff;
          font-weight: 500;
          font-size: 14px;
          cursor: pointer;
          -webkit-user-select: none;
          -moz-user-select: none;
          -ms-user-select: none;
          user-select: none;
          margin: 2px 0px 10px 29px;
          padding: 4px 0 0 0px;

          .ypq {
            display: block;
            width: 42px;
            height: 20px;
            text-align: center;
            background: rgb(46, 134, 107);
            margin: -20px 0px 0px 158px;
            line-height: 20px;
            color: rgb(149, 243, 186);
            border-radius: 6px;
          }

          .video {
            display: inline-block;
            width: 36px;
            height: 20px;
            color: #fff;
            position: absolute;
            top: 16%;
            left: 98%;
            background: rgb(46, 134, 107);
            color: rgb(149, 243, 186);
          }
        }

        .el-form {
          width: 368px;
          margin: 0 0 0 -66px;
        }

        // 按钮组
        .btn-group {
          margin: 28px 0 0 26px;
          text-align: left !important;
        }
      }
    }
  }
}
</style>



